<template>
  <div v-if="bookDetail.length == 0?false:true" class="book-detail">
    <h1>{{bookDetail[0].title}}</h1> 
    <!-- 简介 -->
    <div class="summary clearfix">
      <div class="summary-left">
        <five-star :star="bookDetail[0].rating.average*10/2"></five-star>
        {{bookDetail[0].rating.average == 0?"":bookDetail[0].rating.average}}
        <span class="summary-ratings-count">{{bookDetail[0].rating.numRaters}}人评价</span>
        <p class="summary-genres">
          <span>{{bookDetail[0].author[0]}}</span> / / {{bookDetail[0].publisher}} / {{bookDetail[0].pages}} / {{bookDetail[0].binding}} / {{bookDetail[0].price}} / {{bookDetail[0].pubdate}}
        </p>
      </div>
      <div class="summary-right"><img :src="$common.getImage(bookDetail[0].images.medium)" alt=""/></div>
    </div>
    <div class="btn clearfix">
      <button class="pull-left">想读</button>
      <button class="pull-left">在读</button>
      <button class="pull-left">读过</button>
    </div> 
    <!-- 简介详细  -->
    <div class="summary-detail">
      <h2>{{bookDetail[0].title}}的剧情简介</h2>
      <p>{{bookDetail[0].summary}}</p>
    </div>

    <footer>
      <douban-footer></douban-footer>
    </footer>
  </div>
</template>

<script>
import fivestar from "../components/fivestar"
import doubanFooter from "./footer"
import $ from '../../static/js/jquery-1.11.3.min.js'

export default {
  data: function(){
    return {
      bookDetail: []
    }
  },
  created: function(){
    this.$common.scrollToTop();
  },
  mounted: function(){
    let id = this.$route.params.id;
    this.getAxios('https://api.douban.com/v2/book/'+id,this.bookDetail);
  },
  activated: function(){
    this.$common.scrollToTop();
    var id = this.$route.params.id;
    this.bookDetail.length = 0;
    this.getAxios('https://api.douban.com/v2/book/'+id,this.bookDetail);
  },
  methods: {
    getAxios: function(url,obj){
      $.ajax({
          url:url,
          type: "GET",
          cache: false,
          dataType: "jsonp",
          jsonp: "callback",
          success: function(data){
            for(var i =0;i<8;i++){
              obj.push(data);
            } 
          },
          error: function(jqXHR){
            console.log("发生错误："+jqXHR.status);
          }
      });
      // this.$http.get(url)
      // .then((response) => {
      //   if(response.status == 200){
      //     obj.push(response.data);
      //   }
      // })
      // .catch(function (error) {
      //   console.log(error);
      // });
    }
  },
  components: {
    'five-star': fivestar,
    'douban-footer': doubanFooter
  }
}
</script>

<style scoped>
.book-detail h1 {
  font-size: 1.5rem;
  margin-bottom: 5px;
}

.summary .summary-left {
  width: 65%;
  font-size: 1.2rem;
  float: left;
}

.summary .summary-left .summary-ratings-count {
  color:#aaa;
}

.summary-genres {
  line-height: 1.5;
  margin-top: 10px;
}

.summary-genres span {
  color: #42bd56;
}

.summary .summary-right {
  width: 30%;
  float: right;
}

.summary .summary-right img {
  width: 100%;
}

.btn {
  margin: 60px 0 30px 0;
}

.btn button {
  width: 32%;
  padding:5px 0;
  background-color: #fff;
  border: 1px solid #EEB422;
  color:#EEB422;
  border-radius: 5px;
  font-size: 1.3rem;
}

.btn button + button {
  margin-left: 2%;
}

.summary-detail h2 {
  font-size: 1.2rem;
  color:#aaa;
  margin-bottom: 15px;
}

.summary-detail p {
  font-size: 1.3rem;
  line-height: 1.6;
}
</style>



