<template>
  <div v-if="musicDetail.length == 0?false:true" class="music-detail">
    <h1>{{musicDetail[0].title}}</h1> 
    <!-- 简介 -->
    <div class="summary clearfix">
      <div class="summary-left">
        <five-star :star="musicDetail[0].rating.average*10/2"></five-star>
        {{musicDetail[0].rating.average == 0?"":musicDetail[0].rating.average}}
        <span class="summary-ratings-count">{{musicDetail[0].rating.numRaters}}人评价</span>
        <p class="summary-genres">
          <span>{{musicDetail[0].author[0].name}}</span> / / {{musicDetail[0].attrs.publisher[0]}} / {{musicDetail[0].attrs.pubdate[0]}} 
        </p>
      </div>
      <div class="summary-right"><img :src="$common.getImage(musicDetail[0].image)" alt=""/></div>
    </div>
    <div class="btn clearfix">
      <button class="pull-left">想听</button>
      <button class="pull-left">在听</button>
      <button class="pull-left">听过</button>
    </div> 
    <!-- 简介详细  -->
    <div class="summary-detail">
      <h2>唱片简介</h2>
      <p>{{musicDetail[0].summary}}</p>
    </div>
    <!-- 曲目列鞭炮  -->
    <div class="music-list">
      <h2>{{musicDetail[0].title}}的曲目列表</h2>
      <p><pre>{{musicDetail[0].attrs.tracks[0]}}</pre></p>
    </div>
    <footer>
      <douban-footer></douban-footer>
    </footer>
  </div>
</template>

<script>
import fivestar from "../components/fivestar"
import doubanFooter from "./footer"
import $ from '../../static/js/jquery-1.11.3.min.js'

export default {
  data: function(){
    return {
      musicDetail: []
    }
  },
  created: function(){
    this.$common.scrollToTop();
  },
  mounted: function(){
    let id = this.$route.params.id;
    this.getAxios('https://api.douban.com/v2/music/'+id,this.musicDetail);
    localStorage.flag = false;
  },
  activated: function(){
    this.$common.scrollToTop();
    var id = this.$route.params.id;
    this.musicDetail.length = 0;
    this.getAxios('https://api.douban.com/v2/music/'+id,this.musicDetail);
  },
  methods: {
    getAxios: function(url,obj){
      $.ajax({
          url:url,
          type: "GET",
          cache: false,
          dataType: "jsonp",
          jsonp: "callback",
          success: function(data){
            obj.push(data);
          },
          error: function(jqXHR){
            console.log("发生错误："+jqXHR.status);
          }
      });
      // this.$http.get(url)
      // .then((response) => {
      //   if(response.status == 200){
      //     obj.push(response.data);
      //   }
      // })
      // .catch(function (error) {
      //   console.log(error);
      // });
    }
  },
  components: {
    'five-star': fivestar,
    'douban-footer': doubanFooter
  }
}
</script>

<style scoped>
.music-detail h1 {
  font-size: 1.5rem;
  margin-bottom: 5px;
}

.summary .summary-left {
  width: 65%;
  font-size: 1.2rem;
  float: left;
}

.summary .summary-left .summary-ratings-count {
  color:#aaa;
}

.summary-genres {
  line-height: 1.5;
  margin-top: 10px;
}

.summary-genres span {
  color: #42bd56;
}

.summary .summary-right {
  width: 30%;
  float: right;
}

.summary .summary-right img {
  width: 100%;
}

.btn {
  margin: 60px 0 30px 0;
}

.btn button {
  width: 32%;
  padding:5px 0;
  background-color: #fff;
  border: 1px solid #EEB422;
  color:#EEB422;
  border-radius: 5px;
  font-size: 1.3rem;
}

.btn button + button {
  margin-left: 2%;
}

.summary-detail h2 {
  font-size: 1.2rem;
  color:#aaa;
  margin-bottom: 15px;
}

.summary-detail p {
  font-size: 1.3rem;
  line-height: 1.6;
}

.music-list h2 {
  font-size: 1.2rem;
  color:#aaa;
  margin: 15px 0;
}

.music-list p pre {
  font-size: 1.3rem;
  color:#333;
}
</style>



