<template>
  <div style="padding-bottom:80px;">
    <douban-header></douban-header>
    <div class="search-content">
      <form action="" @submit.prevent>
        <input @search="searchContent" type="search" v-model="val" placeholder="搜索 图书/电影/音乐" />
      </form>
      <button @click="searchContent">搜索</button>
    </div>
    <!-- 音乐 -->
    <div v-if="(music.length == 0 || book.length == 0 || movie.length == 0)?false:true">
      <div v-show="musicAll[0].length==0?false:true" class="music getBox">
        <h2>音乐</h2>
        <ul>
          <router-link :to="{name:'musicDetail',params:{id:item.id}}" tag="li" v-for="item in defaultMusic" :key="item.id">
            <img class="getBox-img" :src="$common.getImage(item.image)" :alt="item.alt"/>
            <div class="getBox-title">
              <h3 class="nowrap">{{item.tags.length?item.tags[0].name:''}}</h3>
              <span>
                <five-star :star="(item.rating.average)*10/2"></five-star>
                {{item.rating.average == 0?'':item.rating.average}}
              </span>
            </div>
          </router-link>
        </ul>
        <div @click="loadingMore(defaultMusic,musicAll[0])" v-if="musicAll[0].length<=3?false:true" class="more">
          <div v-show="musicAll[0].length - defaultMusic.length==0?false:true">更多音乐结果({{musicAll[0].length - defaultMusic.length}})</div>
        </div>
      </div>
      <!-- 图书 -->
      <div v-show="bookAll[0].length==0?false:true" class="book getBox">
        <h2>图书</h2>
        <ul>
          <router-link :to="{name:'bookDetail',params:{id:item.id}}" tag="li" v-for="item in defaultBook" :key="item.id">
            <img class="getBox-img" :src="$common.getImage(item.images.medium)" :alt="item.alt"/>
            <div class="getBox-title">
              <h3 class="nowrap">{{item.title}}</h3>
              <span>
                <five-star :star="(item.rating.average)*10/2"></five-star>
                {{item.rating.average == 0?'':item.rating.average}}
              </span>
            </div>
          </router-link>
        </ul>
        <div @click="loadingMore(defaultBook,bookAll[0])" v-if="bookAll[0].length<=3?false:true" class="more">
          <div v-show="bookAll[0].length - defaultBook.length==0?false:true">更多图书结果({{bookAll[0].length - defaultBook.length}})</div>
        </div>
      </div>
      <!-- 电影 -->
      <div v-show="movieAll[0].length==0?false:true" class="movie getBox">
        <h2>电影</h2>
        <ul>
          <router-link tag="li" :to="{name:'filmDetail',params:{id:item.id}}" v-for="item in defaultMovie" :key="item.id">
            <img class="getBox-img" :src="$common.getImage(item.images.medium)" :alt="item.alt"/>
            <div class="getBox-title">
              <h3 class="nowrap">{{item.title}}</h3>
              <span>
                <five-star  :star="(item.rating.average)*10/2"></five-star>
                {{item.rating.average == 0?'':item.rating.average}}
              </span>
            </div>
          </router-link>
        </ul>
        <div @click="loadingMore(defaultMovie,movieAll[0])" v-if="movieAll[0].length<=3?false:true" class="more">
          <div v-show="movieAll[0].length - defaultMovie.length==0?false:true">更多电影结果({{movieAll[0].length - defaultMovie.length}})</div>
        </div>
      </div>
    </div>
    <div class="unresult" v-else>没有搜索到，换个关键词吧。</div>
  </div>
</template>

<script>
import header from "./header"
import fivestar from "../components/fivestar"
import $ from '../../static/js/jquery-1.11.3.min.js'

export default {
  data: function(){
    return {
      music: [],
      musicAll: [],
      defaultMusic: [],
      book: [],
      bookAll: [],
      defaultBook: [],
      movie: [],
      movieAll: [],
      defaultMovie: [],
      val: '',
      flag: true
    }
  },
  created: function(){
    this.$common.scrollToTop();
  },
  mounted: function(){
    var param = localStorage.getParam;
    this.val = param;
    $("body").css("overflow","auto");
    if(this.val != ''){
      this.getAxios('https://api.douban.com/v2/music/search?q='+param,this.music);
      this.getAxios('https://api.douban.com/v2/book/search?q='+param,this.book);
      this.getAxios('https://api.douban.com/v2/movie/search?q='+param,this.movie);
    }
  },
  methods: {
    getAxios: function(url,obj){
      $.ajax({
          url:url,
          type: "GET",
          cache: false,
          dataType: "jsonp",
          jsonp: "callback",
          success: function(data){
            obj.push(data);
          },
          error: function(jqXHR){
            console.log("发生错误："+jqXHR.status);
          }
      });
      // this.$http.get(url)
      // .then((response) => {
      //   if(response.status == 200){
      //     obj.push(response.data);
      //     console.log(obj);
      //   }
      // })
      // .catch(function (error) {
      //   console.log(error);
      // });
    },
    defLen: function(obj,num,def){
      var len = obj.length;
      len = Math.min(len,num);
      for(var i = 0;i < len;i++){
        def.push(obj[i]);
      }
    },
    loadingMore: function(def,obj){
      var Len = def.length;
      var max = Math.min(Len+6,obj.length);
      for (var i = Len;i < max;i++) {
        def.push(obj[i]);
      }
    },
    searchContent: function(){
      let param = this.val;
      if(this.val != ''){
        localStorage.getParam = this.val,
        this.music.length = 0,
        this.musicAll.length = 0,
        this.defaultMusic.length = 0,
        this.book.length = 0,
        this.bookAll.length = 0,
        this.defaultBook.length = 0,
        this.movie.length = 0,
        this.movieAll.length = 0,
        this.defaultMovie.length = 0;
        this.getAxios('https://api.douban.com/v2/music/search?q='+param,this.music);
        this.getAxios('https://api.douban.com/v2/book/search?q='+param,this.book);
        this.getAxios('https://api.douban.com/v2/movie/search?q='+param,this.movie);
      }
    }
  },
  components: {
    'douban-header': header,
    'five-star': fivestar
  },
  watch: {
    music: function(par){
      this.defLen(par[0].musics,3,this.defaultMusic);
      this.musicAll.push(par[0].musics);
    },
    book: function(par){
      this.defLen(par[0].books,3,this.defaultBook);
      this.bookAll.push(par[0].books);
    },
    movie: function(par){
      this.defLen(par[0].subjects,3,this.defaultMovie);
      this.movieAll.push(par[0].subjects);
    }
  }
}
</script>

<style scoped>
.search-content {
  padding-top: 4rem;
  border-bottom:2px solid #f3f3f3;
}

.search-content form {
  width: 80%;
  display: inline-block;
  margin:8px 0 8px 20px;
  border-radius: 5px;
  background-color: #f3f3f3;
}

.search-content form input {
  width: 100%;
  background-color: #f3f3f3;
  font-size: 1.3rem;
  padding: 10px;
}

.search-content button {
  background-color: #fff;
  font-size: 1.3rem;
  margin-left: 5px;
}

.getBox {
  padding:30px 20px 0 20px;
}

.getBox h2 {
  font-size: 1.4rem;
  color:#aaa;
}

.getBox li {
  padding:10px 0;
  border-bottom: 1px solid #ddd;
}

.getBox .getBox-img {
  width: 7rem;
}

.getBox .getBox-title {
  display: inline-block;
  vertical-align: top;
  width: 65%;
  margin-left: 3%;
}

.getBox-title h3 {
  font-size: 1.5rem;
  margin-bottom: 5px;
}
.getBox-title span {
  color:#aaa;
  font-size: 1.2rem;
}

.more>div{
  padding:10px 0 10px 33%;
  font-size: 1.3rem;
  color:#42bd56;
  border-bottom: 1px solid #ddd;
}

.unresult {
  font-size: 1.2rem;
  color:#aaa;
  text-align: center;
  padding-top: 40px;
}

</style>



