<template>
  <!-- 豆瓣top250 -->
  <div v-if="show.length == 0?false:true" class="most">
    <mt-loadmore :top-method="loadTop" ref="loadmore">
      <h2 class="title">豆瓣top250</h2>  
      <ul
      v-infinite-scroll="loadMoreData"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10" class="clearfix">
        <router-link :to="{name:'filmDetail',params:{id:item.id}}" tag="li" class="image-box pull-left" v-for="item in show" :key="item.id">
          <div class="content-box">
            <div class="img-box"><img :src="$common.getImage(item.images.medium)" :alt="item.alt"/></div>
            <h2 class="nowrap">{{item.title}}</h2>
            <p>
              <five-star :star="item.rating.stars"></five-star>
              <span>{{item.rating.average == 0?'':item.rating.average}}</span>
            </p>
          </div>
        </router-link>
      </ul>
    </mt-loadmore>
  </div>
</template>

<script>
import fivestar from "../components/fivestar"
import $ from '../../static/js/jquery-1.11.3.min.js'
import { Indicator,Toast } from 'mint-ui'

export default {
  data: function(){
    return {
      loading: false,
      top250: [],
      show: []
    }
  },
  components: {
    'five-star': fivestar
  },
  created: function(){
    this.$common.scrollToTop();
  },
  mounted: function(){
    Indicator.open();
    this.getAxios('https://api.douban.com/v2/movie/top250',this.top250,function(){
        Indicator.close();
    });
  },
  watch: {
    top250: function(par){
      for(let i =0;i<9;i++){
        this.show.push(par[i]);
      }
    }
  },
  methods: {
    loadMoreData: function(){
      let showLength = this.show.length;
      let max = Math.min(showLength+9,this.top250.length);
      for (let i = this.show.length; i < max; i++) {
        this.show.push(this.top250[i]);
      }
      if(showLength+9 > this.top250.length ){
        this.loading = true;
        Toast({
          message: '没有更多了',
          position: 'bottom',
          duration: 1000
        });
      }
    },
    loadTop: function(){
      var self = this;
      this.top250.length = 0,
      this.show.length = 0;
      this.getAxios('https://api.douban.com/v2/movie/top250',this.top250,function(){
          self.loading = false;
          self.$refs.loadmore.onTopLoaded();
      });
    },
    getAxios: function(url,obj,complete){
      $.ajax({
          url:url,
          type: "GET",
          cache: false,
          dataType: "jsonp",
          jsonp: "callback",
          success: function(data){
            for(var i =0;i<data.subjects.length;i++){
              obj.push(data.subjects[i]);
            } 
          },
          complete: complete,
          error: function(jqXHR){
            console.log("发生错误："+jqXHR.status);
          }
      });
      // this.$http.get(url)
      // .then(function (response) {
      //   if(response.status === 200){
      //     for(let i =0;i<response.data.subjects.length;i++){
      //       obj.push(response.data.subjects[i]);
      //     }
      //     for(let i =0;i<2;i++){
      //       limit.push(response.data.subjects[i]);
      //     }
      //   }
      // })
      // .catch(function (error) {
      //   console.log(error);
      // });
    }
  }
}
</script>

<style scoped>
.content-box p {
  text-align: center;
}

.content-box p span{
  font-size: 1.2rem;
  color:#aaa;
}
</style>



