<template>
<div>
  <div class="header clearfix">
    <router-link class="pull-left" to="/" tag="div"><img src="../assets/picture/logo.png"></router-link>
    <div class="pull-right">
      <ul class="clearfix">
        <router-link tag="li" to="/page/film" style="color:#2384e8">电影</router-link>
        <router-link tag="li" to="/page/book" style="color:#9f7860">图书</router-link>
        <router-link tag="li" to="/page/broadcast" style="color:#e4a813">广播</router-link>
        <router-link tag="li" to="/page/group" style="color:#2abeda">小组</router-link>
        <li @click="showSearch"><img src="../assets/picture/list.png"></li>
      </ul>
    </div>
  </div>
  <!--搜索界面  -->
  <div class="search">
    <div class="search-content">
      <span @click="closeSearch">关闭</span>
      <form action="" @submit.prevent>
        <input @search="toggle" v-model="val" type="search">
      </form>
    </div>
    <div class="search-list clearfix">
      <ul class="pull-left">
        <li>
          <h2 style="color:#2384e8">电影</h2>
          <p>影院热映</p>
        </li>
        <li>
          <h2 style="color:#e6467e">同城</h2>
          <p>周末活动</p>
        </li>
        <li>
          <h2 style="color:#9f7860">阅读</h2>
          <p>电子书</p>
        </li>
        <li>
          <h2 style="color:#e1644d">东西</h2>
          <p>心爱之物</p>
        </li>
      </ul>
      <ul class="pull-left">
        <li>
          <h2 style="color:#7a6adb">电视</h2>
          <p>正在热播</p>
        </li>
        <li>
          <h2 style="color:#2ab8cc">小组</h2>
          <p>志趣相投</p>
        </li>
        <li>
          <h2 style="color:#5774c5">游戏</h2>
          <p>虚拟世界</p>
        </li>
        <li>
          <h2 style="color:#40cfa9">FM</h2>
          <p>红心歌曲</p>
        </li>
      </ul>
      <ul class="pull-left">
        <li>
          <h2 style="color:#9f7860">图书</h2>
          <p>畅销排行</p>
        </li>
        <li>
          <h2 style="color:#f48f2e">音乐</h2>
          <p>新碟榜</p>
        </li>
        <li>
          <h2 style="color:#596cdd">应用</h2>
          <p>玩手机</p>
        </li>
        <li>
          <h2 style="color:#42bd56">市集</h2>
          <p>购买原创</p>
        </li>
      </ul>
    </div>
    <div class="search-login clearfix">
      <ul class="pull-left">
        <router-link tag="li" to="/register">注册账号</router-link>
      </ul>
      <ul class="pull-right">
        <router-link tag="li" to="/login">登录豆豆</router-link>
      </ul>
    </div>
  </div>
</div>
</template>

<script>
import $ from '../../static/js/jquery-1.11.3.min.js'

export default {
  data: function(){
    return {
      val: ''
    }
  },
  methods: {
    toggle: function(){
      let val = this.val;
      localStorage.getParam = val;
      this.$router.push('/get');
    },
    back: function(){
      this.$router.go(-1);
    },
    showSearch: function(){
      $(".search").animate({left: 0},600,function(){
        $("body").css("overflow","hidden");
      })
    },
    closeSearch: function(){
      $(".search").animate({left: 100+"%"},600,function(){
        $("body").css("overflow","auto");
      })
    }
  }
}
</script>

<style scoped>
.header {
  position: fixed;
  top:0;
  height: 4rem;
  line-height: 4rem;
  background: #fff;
  width: 100%;
  max-width: 768px;
  min-width: 320px;
  border-bottom: 1px solid #f3f3f3;
  padding:0 1.5rem;
  z-index: 9999;
}

.header .pull-left {
  width: 4.5rem;
}

.header .pull-left img {
  width: 100%;
  vertical-align: middle;
}

.header ul li {
  float: left;
  font-size: 1.5rem;
}

.header li+li {
  margin-left: 1rem;
}

.header .pull-right li:last-child {
  width: 2.5rem;
}

.header .pull-right li:last-child img {
  width: 100%;
  vertical-align: middle;
}

.search {
  position: fixed;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 10000;
}

.search .search-content {
  padding:10px 0;
  text-align: center;
  border-bottom: 1px solid #f3f3f3;
}

.search .search-content span {
  vertical-align: middle;
  margin-right: 10px;
  color:#42bd56;
  font-size: 1.4rem;
}

.search .search-content form {
  width:80%;
  display: inline-block;
  border-radius: 5px;
  padding:8px 10px;
  background: #f3f3f3;
  vertical-align: middle;
}

.search .search-content form input {
  width:100%;
  background: #f3f3f3;
  font-size: 1.4rem;
}

.search .search-list ul {
  width: 33.33%;
  text-align: center;
}

.search .search-list ul li {
  margin-top: 20px;
}

.search .search-list ul h2 {
  font-size: 1.8rem;
}

.search .search-list ul p {
  font-size: 1.2rem;
  color:#999;
}

.search .search-login ul {
  width: 50%;
  text-align: center;
  margin-top:30px;
}

.search .search-login ul li {
  font-size: 1.4rem;
  color:#42bd56;
  padding:10px 0;
}
</style>



